.message {
    display: flex;
    flex-direction: column-reverse;
    align-items: flex-end;

}

.imageAndText {
    display: flex;
}


.imageAndText img {
    width: 50px;
    height: 50px;
    border-radius: 30px;
    display: inline-block;
    order: 1;
}

.text {
    display: flex;
    flex-direction: column;
    background-color: #0066CC;
    padding: 3px 13px 25px 8px;
    color: white;
    border-radius: 20px;
}

.text:after {

}

.name {
    color: white;
    text-align: right;
    padding-top: 7px;
}

.messageText {

}

.time {
    order: -1
}